import { useState, useEffect } from "react";
import gamedata from "../data/GameData";  // 이미 있는 데이터 import
import { Container, Row } from "react-bootstrap";
import GameItem from "../components/GameItem";

const ListPage: React.FC = () => {
    const [games, setGames] = useState(gamedata);  // 초기 상태를 gamedata로 설정

    // 초기 상태로 이미 데이터를 가지고 있으므로 useEffect는 생략할 수 있습니다.

    return (
        <div className="App">
            <Container>
                <div className="project_header_container">
                    <h1 className="project_header">가볍게 즐기는 게임</h1>
                </div>
                <Row className="text-center">
                    {
                        games.length > 0 ? (
                            games.map((game) => (
                                <GameItem key={game.id} game={game} />
                            ))
                        ) : (
                            <div>게임 데이터가 없습니다.</div>  // 데이터가 없으면 메시지 표시
                        )
                    }
                </Row>
            </Container>
        </div>
    );
};

export default ListPage;
